.App{
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 10vh 80vh 10vh;
 

}

.header{
  grid-row-start: 1;
  grid-row-end: 2;
  grid-column: 1/-1;
  display: grid;
  grid-template-rows: 1fr;
}
.body{
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column: 1/-1;
}

.footer{
  grid-row-start: 3;
  grid-row-end: 4;
  grid-column: 1/-1;
  display: grid;
  grid-template-rows: 1fr;
}

.head{
  background: rgb(47, 62, 5, 0.9);
  grid-row-start: 1;
  grid-row-end: 2;
}
.foot{
  background: rgb(47, 62, 5, 0.9);
  grid-row-start: 1;
  grid-row-end: 2;
}

.body{
  grid-row-start: 2;
  grid-row-end: 3;
  grid-column: 1/-1;

}
