.body-container{

    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    border: 5px solid rgb(255, 255, 255);
    border-radius: 25px;

    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 4fr 4fr 1fr 4fr 1fr;
    z-index: 3;

}



.body-container img{
    border-radius: 25px;
    grid-row-start: 1;
    grid-row-end: 6;
    grid-column-start: 1;
    grid-column-end: 5;
}

#one{
    grid-row-start: 1;
    grid-row-end: 4;
    grid-column-start: 1;
    grid-column-end: 2;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;


}
#two{
    grid-row-start: 1;
    grid-row-end: 4;
    grid-column-start: 2;
    grid-column-end: 3;

    
}
#three{
    grid-row-start: 1;
    grid-row-end: 4;
    grid-column-start: 3;
    grid-column-end: 5;

    display: flex;
    flex-direction: column;
    align-items: center;

}
#four{
    grid-row-start: 4;
    grid-row-end: 5;
    grid-column-start: 1;
    grid-column-end: 5;
    
}
#five{
    grid-row-start: 5;
    grid-row-end: 6;
    grid-column-start: 1;
    grid-column-end: 5;
}

.head{
    display: grid;
    grid-template-columns: 2% 30% 20% 20% 20% 5%;
    grid-template-rows: 1fr;
}

.head h2{
    grid-column-start: 2;
    grid-column-end: 3;
    
    color: white;
}

.head h2 span{
    color: #D7E5A5;
    font-size: 36px;
}

.search-box{
   
    position: absolute;
    right: 250px;
    top: 25px;
    border-radius: 20px;
    height: 45px;
    width: 300px;
    background: #fff;

    display: grid;
    grid-template-columns: 10% 15% 5% 20% 25% 25%;
    grid-template-rows:  25% 25% 25% 25%;

    cursor: pointer;
    z-index: 4;

}

.search-box:active{
    opacity: 0.9;
}

.search-box img{
    width: 35px;
    height: 20px;
    border: 2px solid #EDEDED;
    border-radius: 5px;
    margin-top: 10.5px;
    margin-left: 20px;

    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 2;
    grid-row-end: 4;
}

.search-box h2{
    color: #2F3E05;

    min-width: 200px;
    height: 30px;
    font-size: 16px;
    grid-column-start: 4;
    grid-column-end: 5;
    grid-row-start: 2;
    grid-row-end: 4;

    position: relative;
    top: -13px;
}

.header-extension{
    position: absolute;
    right: 250px;
    top: 25px;
    border-radius: 20px;
    min-height: 105px;
    width: 300px;
    background: white;
    z-index: 3;

    display: flex;
    flex-direction: column;
  }

.input-box{
    padding: 10px; 
    margin-top: 45px;
    width: 270px;
    
    display: inline-flex;

}

.input-box input{
    width: 200px;
    margin-left: 10px;
    border: none;
    outline: none;
}

.input-box svg{
    margin-left: 45px;
    cursor: pointer;
}

.list-component{
    padding-left: 20px;
    font-weight: 600;
}
.list-component div{
    cursor: pointer;

}

.list-component div:active{
    opacity: 0.7;
}

.backGround{
    width: 100vw;
    height: 100%;
    z-index: -1;

    filter: blur(8px);
    -webkit-filter: blur(8px);

    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
}

@media(max-width: 1500px) {
    .body-container{
        width: 60%;
    }
}

@media(max-width: 1200px) {
    .body-container{
        width: 70%;
    }
}

@media(max-width: 1000px) {
       .body-container{
         width: 75%;
       }
      .head{
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: center;
        height: 80px;
      }
      .header-extension{
          width: 250px;
      }  
      .input-box svg{
          margin-left: 20px;

      }
      .search-box{
   
        position: absolute;
        right: 25px;
        top: 20px;
        height: 45px;
        width: 300px;
    }
    .header-extension{
        right: 25px;
        width: 300px;
        top: 20px;
    }
}

@media(max-width: 850px) {
    .body-container{
        width: 100%;
    }
}

@media(max-width: 650px) {
    .body-container{
        width: 100%;
        height: 100%;
        border-radius: 0px;
        border: none;
    }
    .body-container img{
        border-radius: 0px;
    }
  
  
#one{
    grid-row-start: 1;
    grid-row-end: 2;
    grid-column-start: 1;
    grid-column-end: 5;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    padding-left: 20px;
    padding-top: 20px;
    
}
.box{
    border-radius: 6px;
    margin-top: 5px;
 
}
.box2{
    position: absolute;
    left: 90px;
    border-radius: 6px;
    min-width: 80px;
    padding-left: 10px;
    padding-right: 10px;
    height:22px;

    display: flex;
    justify-content: center;
}
.circle{
   border-radius: 6px;
   margin-left: 30px;
   margin-top: 5px;
}

#four{
    height: 300px;
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows:  1fr 1fr;
}

.Four-one{
    justify-content: flex-start;  
    margin-left: 20px;
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row-start: 1;
    grid-row-end: 2;
}

.Four-two{
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row-start: 1;
    grid-row-end: 2;
    margin-left: -60px;

}

.Four-three{
    width: 100px;
    grid-row-start: 1;
    grid-row-end: 2;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-start;

    margin-top: 40px;
    margin-left: -80px;
}

.Four-four{
    grid-column-start: 1;
    grid-column-end: 5;
    grid-row-start: 2;
    grid-row-end: 3;

}
#three{
    grid-row-start: 1;
    grid-row-end: 2;
    justify-content: center;
}
}

@media(max-width: 550px){
    .search-box{
        width: 250px;
    }
    .header-extension{
        width: 250px;
    }
    .input-box svg{
        margin: 0px;
    }
}
@media(max-width: 500px) {

    .body-container{
    
        display: grid;
        grid-template-rows: 1fr 5fr 1fr 4fr 1fr;
        z-index: 3;
    
    }
    
   
.Four-one{
   margin-left: 10px;
}
    
.Four-two{
    
    margin-left: -20px;

}
.Four-three{
    margin-left: 0px;
}

#one{
