}

@media(max-width: 400px){
  
    .Four-four{
       
    
        display: flex;
        flex-direction: row;
        justify-content: center;
    
    }
    .Four-four{

        
        position: relative;
        left: 50%;
        transform: translate(-50%);
        width: 70%;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    
        
    }
    .Four-four div ul{
        margin-left: 5px;
    }
