     }
}

@media(max-width: 300px){

    .input-box{
        width: 100%;
        margin-left: 7px;
        border-radius: 0px;
    
    }
    .search-box{
        width: 100%;
        padding: 0px;
        margin: 0px;
        left: 0px;
        right: 0px;
        border-radius: 0px;

        margin-left: 7px;
    }
    .header-extension{
        width: 100%;
        padding: 0px;
        margin: 0px;
        left: 0px;
