

#three h2{
   color: white;
}

#three p{
    color: white;
    margin-top: -20px;
}

.box{
    background: #D7E5A5;
    border-radius: 10px;
    width: 90px;
    height:30px;
    z-index: 3;

    display: flex;
    align-items: center;
}

.circle{
    background: #D7E5A5;
    width: 25px;
    height: 25px;
    border-radius: 100%;
    cursor: pointer;
}

.circle:active{
    opacity: 0.6;
}
.arrow{
    color: black;
    position: relative;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
.box2{
    position: absolute;
    left: 110px;
    background: #2F3E05;
    border-radius: 10px;
    min-width: 80px;
    padding-left: 10px;
    padding-right: 10px;
    height:22px;

    display: flex;
    justify-content: center;
}
.box span{
    position: relative;
    left: 10px;
}
.box2 span{
    color: white;
    left: 0px;
}

#four{
    display: grid;
    grid-template-columns: 8% 18% 12% 62%;
    grid-template-rows:  1fr;

}

.Four-one{
    color: white;
    grid-column-start: 1;
    grid-column-end: 2;
    grid-row: 1/-1;

    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0px;
    margin: 0px;

   
}
.Four-one h1{
    
    align-self: flex-start;
    margin-top: -10px;
    padding: 10px;

}
.Four-one ul li{
    width: 90px;
    list-style: none;
    font-size: 9px;
    margin-left: -35px;
}

.Four-two{
    grid-column-start: 2;
    grid-column-end: 3;
    grid-row: 1/-1;
}
.Four-three{
    grid-column-start: 3;
    grid-column-end: 4;
    grid-row: 1/-1;
    color: white;

    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;

}

.Four-three li{
    list-style: none;
    font-size: smaller;
}

.Four-four{
    grid-column-start: 4;
    grid-column-end: 5;
    grid-row: 1/-1;

    display: grid;
    grid-template-columns: 5% 15% 15% 15% 15% 15% 15% 5%;
    grid-template-rows:  1fr;

    
}

#day-one{
    grid-column-start: 2;
    grid-column-end: 3;
}
#day-two{
    grid-column-start: 3;
    grid-column-end: 4;
}
#day-three{
    grid-column-start: 4;
    grid-column-end: 5;
}
#day-four{
    grid-column-start: 5;
    grid-column-end: 6;
}
#day-five{
    grid-column-start: 6;
    grid-column-end: 7;
}
#day-six{
    grid-column-start: 7;
    grid-column-end: 8;
}

.Four-four div ul{

    display: flex;
    flex-direction: column;
    align-items: center;

}

.Four-four div ul li{
    
    list-style: none;
    font-size: 12px;
    color: white;
    margin-left: -35px;
    padding-top: 4px;
    font-size: 10px;

}

#weather-type{
    display: flex;
    flex-direction: column;
}

#weather-type span{
    font-size: smaller;
}

.icon{
    size: 5em;
}


@media(max-width: 1500px){

    .Four-three{
        margin-left: 40px;
        width: 50px;
    }
}

